











































import is from 'is'
import Utils from '@/utils/index'
import api from '@/utils/api'
import mixin from '@/utils/mixin'
export default {
	mixins:[mixin],
	computed: {
	    userInfo() {
	        return this.$store.state.app.userInfo;
	    }
	},
	data() {
		return {
			indicatorDots: false,
			autoplay: false,
			duration: 500,
			is_show:false,
			// swiper变动的id值
			currenid:0,
			missionList:[{
				title:'',
				award:''
			}],
			// 从首页传来的当前等级的id
			current_id:0,
			// 当前等级的id
			currentid:0
		};
	},
	methods:{
		async init(){
			let bid = uni.getStorageSync("bid");
			console.log(bid)
			Utils.loading('加载中')
			let levelList = await api.getAlltask({bid:bid})
			console.log(levelList)
			if(levelList.status == 1){
				Utils.loaded()
				let list = []
				levelList.data.map((v,i) => {
					if(v.id == this.current_id){
						this.currenid = i
						this.currentid = i
					}
					list.push({
						title:v.grade_title,
						desc:v.description,
						bg:v.grade_bg,
						photo:v.grade_photo,
						award:v.award
					})
				})
				this.missionList = list
			}else{
				Utils.error(levelList.message)
			}
		},
		showModel(){
			let isshow = this.is_show
			console.log(isshow)
			if(isshow){
				this.is_show = false
			}else{
				this.is_show = true
			}
			
		},
		changeVip(e) {
			let vipid = e.detail.current
			this.currenid = vipid
			console.log(this.missionList[this.currenid].title)
		},
		navigateto(type){
			console.log(type)
			uni.navigateTo({
				url:type
			})
		},
		
	},
	onShow(){
		let _this = this
		if(!_this.shouldLogin){
			_this.init()
		}
	},
	onLoad(options){
		let currentid = options.id;
		this.current_id = currentid
	}
	
}
